.container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.09);
}

.bg-login {
    position: fixed;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(3px);
    
    z-index: -1;
}